<template>
    <div class="chart">
        <el-carousel height="500px" :autoplay="false" ref="carousel" class="carousel" indicator-position="none" arrow="always">
            <el-carousel-item>
                <com></com>
            </el-carousel-item>

            <el-carousel-item>
                <cdn :Dataset="dataset" :Season="Season" class="charts"></cdn>
                <el-switch style="display: block" v-model="switchValue" active-color="#67C23A" inactive-color="#409eff" active-text="夏季奥运会" inactive-text="冬季奥运会"> </el-switch>
            </el-carousel-item>
        </el-carousel>

        <swiper ref="swiper" :options="swiperOption" class="swiper" @slideChangeTransitionEnd="selectCountry" @click="swiperClick">
            <swiper-slide v-for="(item, index) in flag" :key="'flag-' + item.country">
                <div class="single">
                    <el-avatar shape="square" fit="fill" :src="item.url" class="avatar"></el-avatar>
                    <div class="text">{{ item.country }}</div>
                </div>
            </swiper-slide>
        </swiper>
    </div>
</template>
<script>
    let data = { Summer: {} };

    import com from "@/components/各国奖牌竞争图.vue";
    import cdn from "@/components/各国奖牌折线图.vue";
    export default {
        components: { cdn, com },
        props: ["where"],
        data() {
            return {
                swiperOption: {
                    speed: 0,
                    initialSlide: 0,
                    slidesPerView: 21, // 显示数量
                    freeMode: true,
                    height: 200,
                    centeredSlides: true,
                },
                flag: [],
                options: [
                    { value: "Summer", label: "夏季奥运会" },
                    { value: "Winter", label: "冬季奥运会" },
                ],
                switchValue: true,
                dataset: {},
                Season: "Summer",
                Country: "中国",
                Countries: [],
            };
        },
        async mounted() {
            data = await fetch("./各国奖牌排名趋势.json").then((res) => res.json());
            this.Countries = [...new Set([...Object.keys(data.Summer), ...Object.keys(data.Winter)])];
            console.log(data);
            let flag = await fetch("./国旗地址.json").then((res) => res.json());
            this.flag = Object.entries(flag).map(([key, value]) => {
                return { country: key, url: value };
            });
            console.log(data[this.Season][this.Country]);
            this.reDraw();
            switch (this.where) {
                case "各国奖牌折线图":
                    this.$refs.carousel.setActiveItem(1);
                    break;
                case "各国奖牌竞争图":
                    this.$refs.carousel.setActiveItem(0);
                    break;
                default:
                    break;
            }
        },
        methods: {
            swiperClick(swiper) {
                const realIndex = swiper.clickedIndex;
                swiper.slideTo(realIndex, 300, false);
                this.selectCountry(swiper);
            },
            selectCountry(i) {
                console.log(i.activeIndex);
                this.$refs.carousel.setActiveItem(1);
                console.log(this.flag[i.activeIndex].country);
                this.Country = this.flag[i.activeIndex].country;
            },
            reDraw() {
                console.log(this.Country, this.Season);
                this.dataset = { data: data[this.Season][this.Country], Country: this.Country, Season: this.Season };
            },
        },
        watch: {
            "$refs.swiper.$swiper.activeIndex": function(val) {
                console.log(val);
            },
            Season() {
                this.reDraw();
            },
            Country() {
                this.reDraw();
            },

            $route(to, from) {
                // 监听路由
                switch (to.params.where) {
                    case "各国奖牌折线图":
                        this.$refs.carousel.setActiveItem(1);
                        break;
                    case "各国奖牌竞争图":
                        this.$refs.carousel.setActiveItem(0);
                        break;
                    default:
                        break;
                }
            },
            switchValue(val) {
                this.Season = val ? "Summer" : "Winter";
            },
        },
    };
</script>
<style scoped lang="less">
    .swiper-slide-active {
        transition: 1s;
        font-weight: bold;
        color: Gold !important;
    }
    .charts {
        flex: 1;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .swiper {
        .single {
            cursor: pointer;
            text-align: center;
        }
        .el-avatar {
            background: #00000000;
        }
        .swiper-slide {
            box-shadow: #e6dfdf82 0px 0px 4px 2px;

            border-radius: 5px;
            height: 5em;
            transition: 1s;
        }
    }
    .carousel {
        flex: 1;
    }
    .page {
        height: 100vh;
    }
</style>
