<template>
    <div class="chart">
        <div ref="chart" class="echarts" style="width:700px;height:400px"></div>
    </div>
</template>

<script>
    import * as echarts from "echarts/core";
    import { TitleComponent, ToolboxComponent, TooltipComponent, GridComponent, LegendComponent, MarkLineComponent, MarkPointComponent } from "echarts/components";
    import { LineChart } from "echarts/charts";
    import { CanvasRenderer } from "echarts/renderers";
    const years = {
        Winter: [1994, 1998, 2002, 2006, 2010, 2014],
        Summer: [1996, 2000, 2004, 2008, 2012, 2016],
    };

    export default {
        props: ["Dataset", "Season"],
        mounted() {
            echarts.use([TitleComponent, ToolboxComponent, TooltipComponent, GridComponent, LegendComponent, LineChart, CanvasRenderer, MarkLineComponent, MarkPointComponent]);

            this.myChart = echarts.init(this.$refs.chart);
        },
        data() {
            return {
                option: {
                    title: {
                        text: "折线图堆叠",
                    },
                    tooltip: {
                        trigger: "axis",
                    },
                    legend: {
                        data: ["金牌", "银牌", "铜牌"],
                    },
                    grid: {
                        left: "3%",
                        right: "4%",
                        bottom: "3%",
                        containLabel: true,
                    },

                    xAxis: {
                        type: "category",
                    },
                    yAxis: {
                        type: "value",
                    },
                    series: [],
                },
            };
        },
        watch: {
            Dataset({ data, Season, Country }) {
                if (!data) {
                    this.$notify({
                        title: "未发现数据",
                        message: "暂无数据哦",
                    });
                    return;
                }
                let { Gold = {}, Bronze = {}, Silver = {} } = data;
                this.option.title.text = Season + "-" + Country + "奖牌折线图";
                this.option.xAxis.data = years[this.Season];
                let series = [
                    {
                        name: "金牌",
                        data: years[this.Season].map((i) => Gold[i] || 0),
                        itemStyle: {
                            normal: {
                                color: "#ffd700", //折线点的颜色
                                lineStyle: {
                                    color: "#ffd700", //折线的颜色
                                },
                            },
                        },
                    },
                    {
                        name: "银牌",
                        data: years[this.Season].map((i) => Silver[i] || 0),
                        itemStyle: {
                            normal: {
                                color: "#c0c0c0", //折线点的颜色
                                lineStyle: {
                                    color: "#c0c0c0", //折线的颜色
                                },
                            },
                        },
                    },
                    {
                        name: "铜牌",
                        data: years[this.Season].map((i) => Bronze[i] || 0),
                        itemStyle: {
                            normal: {
                                color: "#a52a2a", //折线点的颜色
                                lineStyle: {
                                    color: "#a52a2a", //折线的颜色
                                },
                            },
                        },
                    },
                ];

                this.option.series = series;
                this.option.series.map((i) =>
                    Object.assign(i, {
                        type: "line",
                        symbol: "circle", //折线点设置为实心点
                        symbolSize: 4, //折线点的大小
                        markPoint: {
                            data: [{ type: "max", name: "最大值" }],
                        },
                        markLine: {
                            data: [{ type: "average", name: "平均值" }],
                        },
                    })
                );
                // this.option.dataset.dimensions = ["colors", ...years[this.Season]];
                // Gold.colors = "金牌";
                // Bronze.colors = "铜牌";
                // Silver.colors = "银牌";

                // this.option.dataset.source = [Gold, Bronze, Silver];
                console.log(Gold);
                this.myChart.setOption(this.option);
            },
        },
    };
</script>
<style lang="less" scoped></style>
